import React					from 'react'
import { hydrate, render } 		from "react-dom";
import { BrowserRouter, Route, Switch } 		from 'react-router-dom'
// import reportWebVitals 			from './reportWebVitals'
// import Progress 				from './components/UI/NProgress/Progress'
import './index.css'

import Layout 				from './hoc/Layout/Layout'
import Home 				from './containers/Home/Home'
import Category 			from './containers/Category/Category'
import FAQ 					from './containers/FAQ/FAQ'
import Page 				from './containers/Page/Page'
import Services 			from './containers/Services/Services'
import ServicePage 			from './containers/ServicePage/ServicePage'
import classes 				from './App.module.sass'
import Search 				from './containers/Search/Search'

// import ReactGA 					from 'react-ga';

// ReactGA.initialize('UA-000000-01');
// ReactGA.pageview(window.location.pathname + window.location.search);

// const [loading, setLoading] = useState(true)

// useEffect(() => {
// 	setLoading(false)
// }, [])

const APP = (
	<React.StrictMode>
		<BrowserRouter>
			{/* <React.Fragment>
				<Progress isAnimating={loading} />
			</React.Fragment> */}
			<Layout className={classes.App}>
				<Switch>
					<Route exact path="/" 			component={Home} />
					<Route path="/eco-faq" 			component={FAQ} />
					<Route path="/search/" 			component={Search} />
					<Route path="/services/:page" 	component={ServicePage} />
					<Route path="/services/" 		component={Services} />
					<Route path="/:category/:page" 	component={Page} />
					<Route path="/:category" 		component={Category} />
				</Switch>
			</Layout>
		</BrowserRouter>
	</React.StrictMode>
);

const rootElement = document.getElementById("root");
if (rootElement.hasChildNodes()) {
  hydrate(APP, rootElement);
} else {
  render(APP, rootElement);
}


